$(document).ready(function () {
 function tarif(tab) {
  
  
  var timeList = 700;
  var TimeView = 5000;
  var RadioBut = true;
  
  var slideNum = 1;
  var slideTime;
  var coleCount = $(tab + " .animate_register-col .slide-col").length;
  
  
  var animSlide = function (arrow) {
    
    if (arrow == "next") {
      if (slideNum == coleCount) {
        slideNum = 1;
      }
      else {
        slideNum++
      }
      let translateWidth = -$(tab + ' .animate_register').width() * (slideNum - 1);
      $(tab + ' .animate_register-col').css({'transform': 'translate(' + translateWidth + 'px, 0)'});
    }else if(arrow == "prew")
    {
      if(slideNum == 1) {
        slideNum=coleCount;
      }
      else{
        slideNum-=1
      }
      let translateWidth = -$(tab + ' .animate_register').width() * (slideNum - 1);
      $(tab + ' .animate_register-col').css({'transform': 'translate(' + translateWidth + 'px, 0)'});
    }else{
      if(arrow >= slideNum){
        return false;
      }
      slideNum = arrow;
      let translateWidth = -$(tab + ' .animate_register').width() * (slideNum -1);
      $(tab + ' .animate_register-col').css({'transform': 'translate(' + translateWidth + 'px, 0)'});
    }
    $(tab + " .ctrl-select.active").removeClass("active");
    $(tab + ' .ctrl-select').eq(slideNum - 1).addClass('active');
    
  }
  
  $(tab + ' .next-col').click(function () {
    animSlide("next");
    var destination = $('.tabs_chang_section').offset().top;
    $('html').animate({ scrollTop: destination }, 1100);
    console.log(this);
    return false;
  })
  
  
  $('.pref-col').click(function () {
    animSlide("prew");
    return false;
  })
  $(tab + " .ctrl-select:first").addClass("active");
  $(tab + ' .ctrl-select').click(function(){
    var goToNum = parseFloat($(this).text());
    animSlide(goToNum);
  });
 }
  $(function () {
    $('.tarifs_chang').on('click', '.tarifs_box__text:not(.tarifs_box__active)', function () {
      $(this)
        .addClass('tarifs_box__active').siblings().removeClass('tarifs_box__active').closest('div.tabs').find('div.tabstarif-content').removeClass('active').eq($(this).index()).addClass('active');
      if($('.tarifs_box__text:first').hasClass('tarifs_box__active')){
        $('.tarifs_check').prop('checked', true);
        if($('#tab1').hasClass('active')){
          tarif('#tab1');
        }
      }else if($('.tarifs_box__text:last').hasClass('tarifs_box__active')){
        $('.tarifs_check').prop('checked', false);
        if($('#tab2').hasClass('active')){
          tarif('#tab2');
        }
      }
      
    });
  });
  
  if($('#tab1').hasClass('active')){
    tarif('#tab1');
  }
  
 
 
});
